body {
  font-family: Montserrat, sans-serif;
}

.box__cat {
  flex-direction: row;
  justify-content: center;
  display: flex;
}

.box__cat > img {
  max-width: 500px;
  margin-right: 40px;
}

.box__description {
  max-width: 500px;
}

h2 {
  font-size: 2.5rem;
}

.breed-select {
  cursor: pointer;
  text-align: center;
  width: 300px;
  height: 50px;
  background-color: #e3c2b9;
  border-radius: 20px;
  margin: 50px auto;
  font-family: Montserrat, sans-serif;
  font-size: 20px;
  display: flex;
}

.loader {
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 50%;
  margin: 200px auto;
  animation: rotation 1s linear infinite;
  display: flex;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 15px;
  height: 15px;
  background: #e3c2b9;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(150%, 150%);
}

.loader:before {
  top: auto;
  bottom: 0;
  left: auto;
  right: 0;
  transform: translate(-150%, -150%);
}

.loader__text {
  justify-content: space-around;
  display: flex;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}

